import _forEach from 'lodash.foreach'
import _clonedeep from 'lodash.clonedeep'
import _isequal from 'lodash.isequal'

export default {
  props: {
    /**
     * @description dialog
     */
    formOptions: {
      type: Object,
      default () {
        return {}
      }
    },
    /**
     * @description dialog
     */
    viewTitle: {
      type: String,
      default: ''
    },
    /**
     * @description dialog
     */
    addTitle: {
      type: String,
      default: ''
    },
    /**
     * @description dialog
     */
    editTitle: {
      type: String,
      default: ''
    },
    /**
     * @description 
     */
    addTemplate: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    editTemplate: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    addRules: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    editRules: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      /**
       * @description dialog
       */
      isDialogShow: false,
      isFormShow: false,
      /**
       * @description 
       */
      formData: {},
      formDataBefore: {},
      /**
       * @description 
       */
      formMode: 'edit',
      /**
       * @description editTemplate
       */
      formDataStorage: {},
      /**
       * @description 
       */
      formTemplateStorage: {},
      formModeContext: null,
      formTemplateGroupStorage: {},
      formGroupsActive: []
    }
  },
  computed: {
    formMaxHeight () {
      if (this.formOptions && this.formOptions.maxHeight) {
        const maxHeight = this.formOptions.maxHeight
        if (!isNaN(maxHeight)) {
          return maxHeight + 'px'
        }
        return maxHeight
      }
      return null
    }
  },
  methods: {
    getFormTitle () {
      if (this.formMode === 'add') {
        return this.addTitle
      } else if (this.formMode === 'edit') {
        return this.editTitle
      } else if (this.formMode === 'view') {
        return this.viewTitle
      } else {
        return this.editTitle
      }
    },
    getFormData () {
      return this.formData
    },
    async buildFormData (index, row, templage, modeContext) {
      if (templage == null) {
        console.warn('template,mode:', this.formMode)
        templage = {}
      }
      const formData = {}
      this.formTemplateStorage = templage ? _clonedeep(templage) : {}
      delete this.formTemplateStorage.__group__
      this.formTemplateGroupStorage = templage.__group__ ? _clonedeep(templage.__group__) : undefined
      const tempGroups = {
        default: { columns: this.formTemplateStorage }
      }
      if (this.formTemplateGroupStorage) {
        _forEach(this.formTemplateGroupStorage.groups, (value, key) => {
          tempGroups[key] = value
        })
      }
      let newRow = await this.fetchDetail(index, row, this.formMode, modeContext)
      newRow = newRow || {}
      this.formDataStorage = newRow
      const formGroupsActive = []
      _forEach(tempGroups, (group, groupKey) => {
        if (!group.collapsed) {
          formGroupsActive.push(groupKey)
        }
        _forEach(group.columns, (template, key) => {
          let value = this._get(newRow, key)
          // 
          if (template && value === undefined) {
            if (template.value != null) {
              value = template.value
            } else if (template.component) {
              if (template.component.value != null) {
                value = template.component.value
              } else if (template.component.props && template.component.props.value != null) {
                value = template.component.props.value
              }
            }
          }
          this._set(formData, key, value)
        })
      })
      this.$set(this, 'formGroupsActive', formGroupsActive)
      this.$set(this, 'formData', formData)
      if (this.formOptions.saveRemind) {
        this.$set(this, 'formDataBefore', _clonedeep(formData))
      }

      return {
        newRow,
        formData,
        formTemplateStorage: this.formTemplateStorage,
        formTemplateGroupStorage: this.formTemplateGroupStorage,
        formDataStorage: this.formDataStorage
      }
    },
    handleDialogShowUpdate (value) {
      if (value) {
        this.isFormShow = true
        this.$nextTick(() => {
          this.isDialogShow = true
        })
      } else {
        this.isDialogShow = false
        this.$nextTick(() => {
          this.isFormShow = false
        })
      }
    },
    async openDialog (index, row, templage, modeContext = null) {
      this.formModeContext = modeContext
      const { newRow, formData } = await this.buildFormData(index, row, templage, modeContext)

      this.handleDialogShowUpdate(true)

      this.$emit('dialog-opened', {
        mode: this.formMode,
        row: newRow,
        form: formData,
        template: this.formTemplateStorage,
        groupTemplate: this.formTemplateGroupStorage,
        modeContext
      })
    },
    fetchDetail (index, row, formMode, modeContext) {
      if (this.options.fetchDetail != null) {
        let ret = this.options.fetchDetail(index, row, formMode, modeContext)
        if (!(ret instanceof Promise)) {
          ret = new Promise(resolve => {
            resolve(ret)
          })
        }
        if (this.options.fetchDetailAppendHandler) {
          ret = ret.then((info) => {
            return this.options.fetchDetailAppendHandler(info, index, row, formMode, modeContext)
          })
        }
        return ret
      } else {
        return new Promise(resolve => {
          if (row != null) {
            resolve(_clonedeep(row))
            return
          }
          resolve(undefined)
        })
      }
    },

    buildAddSubmitData () {
      const rowData = {}
      _forEach(this.formData, (value, key) => {
        this._set(rowData, key, value)
      })
      return rowData
    },
    buildEditSubmitData () {
      const rowData = _clonedeep(this.formDataStorage)
      _forEach(this.formData, (value, key) => {
        if (value == null && this.formOptions && this.formOptions.nullToBlankStr) {
          value = ''
        }
        this._set(rowData, key, value)
      })
      return rowData
    },
    /**
     * @description 
     */
    handleDialogSave () {
      this.$refs.form.validate((valid) => {
        if (!valid) {
          return false
        }
        let rowData = {}
        if (this.formMode === 'edit') {
          rowData = this.buildEditSubmitData()
          this.$emit('row-edit', {
            index: this.editIndex,
            row: rowData,
            modeContext: this.formModeContext
          }, (param = null) => {
            if (param === false) {
              this.handleCloseDialog()
              return
            }
            this.handleDialogSaveDone({
              ...rowData,
              ...param
            })
          })
        } else if (this.formMode === 'add') {
          rowData = this.buildAddSubmitData()
          this.$emit('row-add', rowData, (param = null) => {
            if (param === false) {
              this.handleCloseDialog()
              return
            }
            this.handleDialogSaveDone({
              ...rowData,
              ...param
            })
          }, { modeContext: this.modeContext })
        } else if (this.formMode === 'view') {
          this.handleDialogSaveDone(rowData)
        } else {
          // 
          rowData = this.formData
          this.$emit('row-' + this.formMode, {
            index: this.editIndex,
            row: this.formDataStorage,
            form: rowData,
            modeContext: this.formModeContext
          }, (param = null) => {
            if (param === false) {
              this.handleCloseDialog()
              return
            }
            this.handleDialogSaveDone({
              ...rowData,
              ...param
            })
          })
        }
      })
    },
    /**
     * @description 
     */
    handleDialogCancel (done) {
      if (this.formOptions.saveRemind && this.formMode !== 'view') {
        if (!_isequal(this.formData, this.formDataBefore)) {
          // 
          let confirm = this.$confirm('', '', {
            distinguishCancelAndClose: true,
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })
          if (this.formOptions.saveRemind instanceof Function) {
            confirm = this.formOptions.saveRemind()
          }
          return confirm.then(() => {
            this.handleDialogSave()
          }).catch((action) => {
            if (action === 'cancel') {
              // 
              this.$emit('dialog-cancel', done)
              this.handleDialogClosed()
            } else {
              // 
            }
          })
        }
      }
      this.$emit('dialog-cancel', done)

      this.handleDialogClosed()
    },
    /**
     * @description 
     */
    handleDialogSaveDone (rowData) {
      if (this.formOptions.updateTableDataAfterEdit !== false) {
        if (this.formMode === 'edit') {
          this.handleUpdateRow(this.editIndex, rowData)
        } else if (this.formMode === 'add') {
          this.handleAddRow(rowData)
        }
      }
      this.handleCloseDialog()
      this.formDataStorage = {}
    },
    /**
     * @description 
     */
    handleCloseDialog () {
      this.handleDialogShowUpdate(false)
      this.handleDialogClosed()
      this.formDataBefore = null
    },
    handleDialogClosed () {
      this.$emit('dialog-closed', {
        mode: this.formMode,
        row: this.formDataStorage,
        form: this.formData,
        modeContext: this.formModeContext
      })
    }
  }
}
